.containerHeadInf,
.containerHeadInfBegin {
  background-color: white;
  min-width: 50vw;
  min-height: 50svh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 20px 30px;
  align-items: center;
  border-radius: 20px;
  position: relative;
  gap: 20px;
}
.containerHeadInfBegin {
  padding: 0;
}
.patientHeaderInfo {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  margin-bottom: 100px;
}
.containerHeadInf h2,
.patientHeaderInfo h2,
.containerHeadInfBegin h2 {
  color: #003d8a;
  text-align: center;
}
.patientHeaderInfo img {
  max-width: 210px;
}
.patientHeaderInfo p {
  max-width: 500px;
  text-align: center;
  font-weight: bold;
  font-size: 20px;
}
.containerHeadInfBegin {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
.footerButtons {
  display: flex;
  justify-content: space-between;
  width: 100%;
}
.firstVisitInfo {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  gap: 10px;
}
.firstVisitInfo section {
  border: 1px solid #003d8a;
  padding: 30px 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 10px;
  border-radius: 20px;
  cursor: pointer;
  transition: 0.5s;
}
.firstVisitInfo section:hover {
  transform: scale(1.05);
}
.currentVariants {
  display: grid;
  grid-template-columns: max-content max-content;
  grid-column-gap: 30px;
  grid-row-gap: 10px;
}
.summaryBlock {
  max-width: 600px;
  display: flex;
  flex-direction: column;
  gap: 50px;
}
.summaryBlock ul {
  margin-left: 20px;
}
.summaryBlock h3 {
  margin-right: auto;
}

@media (max-width: 1150px) {
  .currentVariants {
    grid-template-columns: max-content;
  }
}

@media (max-width: 980px) {
  .firstVisitInfo {
    grid-template-columns: 1fr 1fr;
  }
}
@media (max-width: 720px) {
  .currentVariants {
    grid-template-columns: 350px;
  }
}
@media (max-width: 550px) {
  .firstVisitInfo {
    grid-template-columns: 1fr;
  }
}
@media (max-width: 400px) {
  .currentVariants {
    grid-template-columns: 300px;
  }
}
