.modalcontainer {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  min-height: 43svh;
}
.modalcontainer h2 {
  color: #003d8a;
}
.modalcontainer div {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 5svh;
}
.modalcontainer p {
  margin-right: auto;
  max-width: 43vw;
}
.sectionTypes {
  max-width: 43vw;
}
.alertModal,
.hintModal,
.itsRightModal {
  position: absolute;
  width: 50%;
  height: max-content;
  padding: 30px 0;
  max-height: 90%;
  background: linear-gradient(180deg, rgb(248, 114, 5), rgb(206, 49, 1));
  top: 0;
  bottom: 0;
  margin: auto 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  gap: 10px;
}
.hintModal {
  background: linear-gradient(180deg, rgb(255, 196, 0), rgb(199, 153, 0));
}
.itsRightModal {
  background: linear-gradient(180deg, rgb(43, 255, 1), rgb(26, 153, 1));
}
.attantion,
.hint,
.itsRight {
  font-size: 60px;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100px;
  height: 100px;
  background-color: rgb(206, 49, 1);
  color: rgba(255, 255, 255, 0.795);
  border-radius: 50%;
}
.hint {
  background-color: rgb(199, 153, 0);
}
.itsRight {
  background-color: rgb(26, 153, 1);
  width: 130px;
  height: 130px;
}
.alertModal p,
.hintModal p,
.itsRightModal p {
  color: white;
  font-weight: bold;
  font-size: 24px;
  max-width: 500px;
  text-align: center;
}
.textarea {
  margin-top: 50px;
  margin-bottom: auto;
  width: 80%;
  border-radius: 10px;
  padding: 20px;
  font-size: 18px;
}

@media (max-width: 980px) {
  .modalcontainer p {
    max-width: 90%;
  }
  .sectionTypes {
    max-width: 90%;
  }
  .alertModal,
  .hintModal,
  .itsRightModal {
    width: 95%;
  }
}
